/* 2. Header */
.transparent-header {
	position: absolute;
	width: 100%;
	left: 0;
	top: 0;
	z-index: 9;
}
.header-wrap {
	box-shadow: 0px 19px 55px 0px rgba(98, 81, 146, 0.08);
	margin: 50px 285px 0;
	border-radius: 10px;
	background: #fff;
}
.main-menu ul li {
	display: inline-block;
	margin-left: 45px;
	position: relative;
}
.main-menu ul li:first-child {
	margin-left: 0px;
}
.main-menu ul li a {
	font-size: 16px;
	font-weight: 500;
	color: #58468c;
	text-transform: uppercase;
	display: block;
	padding: 40px 0;
}
.main-menu ul li:hover > a {
	color: #ff4800;
}
.main-menu ul li.active > a {
	color: #ff4800;
}
.main-menu ul li .submenu {
	position: absolute;
	z-index: 9;
	background-color: #fff;
	border-radius: 0;
	border: none;
	box-shadow: 0px 13px 25px -12px rgba(0,0,0,0.25);
	display: block;
	left: 0;
	opacity: 0;
	padding: 18px 0;
	right: 0;
	top: 100%;
	visibility: hidden;
	min-width: 230px;
	border: 1px solid #f5f5f5;
	background: #ffffff;
	box-shadow: 0px 30px 70px 0px rgba(137,139,142,0.15);
	margin: 0;
	-webkit-transform: scale(1 , 0);
	        transform: scale(1 , 0);
	-webkit-transform-origin: 0 0;
	        transform-origin: 0 0;
	transition: all 0.3s ease-in-out;
}
.main-menu ul li .submenu li {
	margin-left: 0;
	text-align: left;
	display: block;
}
.main-menu ul li .submenu li a {
	padding: 0 10px 0 25px;
	line-height: 45px;
	font-weight: 400;
	text-transform: capitalize;
	transition: all 0.3s ease-in-out;
}
.main-menu ul li:hover > .submenu {
	opacity: 1;
	visibility: visible;
	-webkit-transform: scale(1);
	        transform: scale(1);
}
.s-menu-area .main-menu ul li .submenu li a {
	color: #58468c;
}
.main-menu ul li .submenu li:hover > a {
	color: #ff4800;
}
.header-btn .btn {
	padding: 13px 20px;
	margin-left: 40px;
}
.header-btn .btn i {
	margin-right: 8px;
}
.sticky-menu {
    left: 0;
    margin: auto;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 99;
    background: #fff;
    -webkit-animation: 1000ms ease-in-out 0s normal none 1 running fadeInDown;
    animation: 1000ms ease-in-out 0s normal none 1 running fadeInDown;
	box-shadow: 0 10px 15px rgba(25, 25, 25, 0.1);
	border-radius: 0;
}
.sticky-menu .main-menu ul li a {
	padding: 35px 0;
}
.s-menu-area .main-menu ul li a,
.s-menu-area .main-menu ul li.active > a {
	color: #fff;
}
.s-header-wrap {
	border-bottom: 1px dashed #ffffff40;
}
.sticky-menu .s-header-wrap {
	border-bottom: none;
}
.s-transparent-header.sticky-menu {
	background: #2943e0;
}
